import React from 'react';
import {Modal, Input, Table, Pagination, notification, message, Icon,Breadcrumb} from 'antd';
// import Sign from "../../../components/sign/Sign";
import styles from './QueryForm.scss';
import QueryFormPopup from "../../../components/queryFormPopup/QueryFormPopup";
import {getOrderList,getOrderListLikeNameOrRemarks,deleteAOrder,download} from "../../../api/Businessman";
import {ordersDetails,hardwareTopology} from "../../../api/common";
const Search = Input.Search;
class QueryForm extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            filteredInfo: null,
            sortedInfo: null,
            number:2,
            current:0,
            pageSize:10,
            PaginationCurrent:1,
            PaginationPageSize:10,
            searchValue:"",
            searchState:false,
            searchCurrent:0,
            searchPageSize:10,
            deleteVisible:false,
            deleteQueryFormRecord:{},
            queryFormModalVisible:false,
            queryFormRecord:[],
            tableLoading:false
        };
    }
    async componentDidMount() {
        this.setState({tableLoading:true});
      await this.getQueryFormList();
        this.setState({tableLoading:false});
    }
   //下载拓扑图
    getHardwareTopology=async(orderInfo)=>{
        try {
          const result = await  hardwareTopology({
              gpuNumber:orderInfo.ordersEntity.gupServerNumber,
              storeNumber:orderInfo.ordersEntity.storageServerNumber,
              cpuNumber:orderInfo.ordersEntity.cpuServerNumber,
              userNumber:orderInfo.ordersEntity.userNumber,
              ibNumber:orderInfo.ordersEntity.ibNumber,

          });
          console.log(result );
         if(result.errorCode){
             message.error("获取拓扑图失败")
         }
         else{
             let hardwareTopologyUrl=window.URL.createObjectURL(result);
             console.log(hardwareTopologyUrl);
            await this.setState({
                hardwareTopologyUrl:hardwareTopologyUrl
             })
         }
        }

        catch (e) {

        }
    };
    getQueryFormList=async()=>{
        try {
            const info = await getOrderList({
                page:this.state.current,
                size:this.state.pageSize
            });
            console.log(info);
            if (info.errorCode === 1000) {
                this.setState({
                    data: info.data.list,
                    total:info.data.totalCount
                });
            }else{
                notification.info({
                    message: info.errorMessage
                })
            }
        }
        catch (e) {
            // message.error("获取失败")
            console.log("获取失败");
        }
    };
    handleChange = (pagination, filters, sorter) => {
        console.log('Various parameters', pagination, filters, sorter);
        this.setState({
            filteredInfo: filters,
            sortedInfo: sorter,
        });
    };
    //改变页数
    onChange = async (page)=>{
        this.setState({
            tableLoading:true,
            PaginationCurrent:page,
        });
    if(this.state.searchState){
            await this.setState({searchState:page-1});
            console.log(this.state.searchState);
            this.searchQueryList();
        }
    else{
            await this.setState({current:page-1});
            console.log(this.state.current);
            this.getQueryFormList();
        }
        this.setState({
            tableLoading:false,
        });
    };
    //改变每页显示数目
    onShowSizeChange = async(page,pageSize)=>{
        this.setState({
            tableLoading:true,
            PaginationCurrent:page,
            PaginationPageSize:pageSize,
        });
    if(this.state.searchState){
            console.log(pageSize,page);
            await this.setState({
                searchCurrent:page-1,
                searchPageSize:pageSize
            });
            this.searchQueryList();
        }
        else{
            console.log(pageSize,page);
            await this.setState({
                current:page-1,
                pageSize:pageSize
            });
         this.getQueryFormList();
        }
        this.setState({
            tableLoading:false
        });

    };
    //搜索框模糊查询询单
    searchQuery= async (value)=>{
        if(value!==""){
            await  this.setState({
                tableLoading:true,
                searchState:true,
                searchValue:value,
                PaginationCurrent:1,
                PaginationPageSize:10,
            });
            this.searchQueryList();
            this.setState({tableLoading:false});
        }else{
            await  this.setState({
                tableLoading:true,
                searchState:false,
                current:0,
                pageSize:10,
                PaginationCurrent:1,
                PaginationPageSize:10,
            });
            this.getQueryFormList();
            this.setState({tableLoading:false});
        }
    };
    //模糊查询询单
    searchQueryList= async()=>{
        try {
        const info = await getOrderListLikeNameOrRemarks({
            page:this.state.searchCurrent,
            size:this.state.searchPageSize,
            nameOrRemarks:this.state.searchValue
        });
        console.log(info);
        if(info.errorCode===1000){
            this.setState({
                data: info.data.list,
                total:info.data.totalCount
            });
        }
        else{
            notification.info({
                message: info.errorMessage
            })
        }
        }
        catch (e) {
            console.log("获取失败");
        }
    };
    //数据库删除一个询单
    deleteAQueryForm=async()=>{
        try {
            const info = await deleteAOrder({
                "id": this.state.deleteQueryFormRecord.id
            });
            console.log(info);
            this.setState({
                tableLoading: true,
                deleteVisible:false
            });
            if(info.errorCode===1000){
                this.getQueryFormList();
                message.success(`“${this.state.deleteQueryFormRecord.name}”删除成功`);
            }else{
                message.error(`“${this.state.deleteQueryFormRecord.name}”删除失败`);
            }
        }catch (e) {
            message.error(`“${this.state.deleteQueryFormRecord.name}”删除失败`);

        }
        this.setState({tableLoading:false})
    };
    //点击出现删除询单弹出框
    deleteQueryForm=(record)=>{
        this.setState({
            deleteVisible:true,
            deleteQueryFormRecord:record
        })
    };
    //确认删除
    deleteQueryFormOk=()=>{
        this.deleteAQueryForm();
    };
    //取消删除
    deleteQueryFormCancel=()=>{
        this.setState({
            deleteVisible:false
        });
    };
    //订单详情
    getOrdersDetails=async(record)=>{
        try {
            const info = await ordersDetails({
                ordersId: record.id
            });
            console.log(info);
            if(info.errorCode===1000){
              await  this.getHardwareTopology(info.data);
                this.setState({
                    orderInfo:info.data
                })
            }else{
            }
        }catch (e) {
            console.log("异常");
        }
        };
    //点击查看订单详情
    checkQueryForm= async(record)=>{
        await this.getOrdersDetails(record);

        this.setState({
            queryFormModalVisible:true,
            queryFormRecord:record
        });
        console.log(this.state.hardwareTopologyUrl);
    };
    //询单弹出框子组件返回值
    queryFormModalChange=(newState)=>{
        this.setState(newState);
    };
    downloadQueryForm=async(record)=>{
        console.log(record);
       await this.getOrdersDetails(record);
       console.log(this.state.orderInfo);
           let params=this.state.orderInfo.ordersEntity;
            delete  params.businessmanByBusinessmanId;
            if(!params.rackLeasedPrice){params.rackLeasedPrice=null}
           await this.getQueryFormFile(params,record);
    };
    //下载询单
    getQueryFormFile=async(params,record)=>{
        try {
            const info = await download(params);
            console.log(info);
            if(info.errorCode){
                message.error("下载失败")
            }
            else{
                let fileName = record.name;
                if(window.navigator.msSaveOrOpenBlob){
                    navigator.msSaveBlob(info, fileName);
                }
                else {
                    let link = document.createElement("a");
                    link.href = window.URL.createObjectURL(info);
                    link.download=record.name+".doc";
                    link.click();
                    window.URL.revokeObjectURL(link.href);
                }
              //   let downloadElement = document.createElement('a');
              // //创建下载的链接
              //   downloadElement.href = href;
              //   // window.open(href);
              //   downloadElement.download = '询单'; //自定义文件名
              //   document.body.appendChild(downloadElement);
              //   downloadElement.click(); //点击下载
              //   document.body.removeChild(downloadElement); //下载完成移除元素
              //   window.URL.revokeObjectURL(href); //释放掉blob对象
            }
        }catch (e) {
            console.log("异常");
        }
    };
    render() {
        // window.localStorage.setItem("current","queryForm");
        let { sortedInfo} = this.state;
        sortedInfo = sortedInfo || {};
            const columns = [{
                title: '询单名',
                dataIndex: 'name',
                key: 'name',
            },
            {
                title: '创建时间',
                dataIndex: 'registertime',
                key: 'registertime',
                sorter: (a, b) => {return new Date(a.registertime).getTime() -new Date(b.registertime).getTime();},
                sortOrder: sortedInfo.columnKey === 'registertime' && sortedInfo.order,
                className:"formName",
                render: (text,record) => !record.status ? (<span style={{color:"#999999"}}>{text}</span>) : (<span>{text}</span>)
            },
            {
                title: '备注',
                dataIndex: 'remarks',
                key: 'remarks',
                render: (text,record) => !record.status ? (<span style={{color:"#999999"}}>{text}</span>) : (<span>{text}</span>)
            },
            {
                title: '操作',
                dataIndex: 'account',
                key: 'account',
                render: (text,record) => (
               <span>
                <a   onClick={()=>this.checkQueryForm(record)}>查看&nbsp;&nbsp;</a>
                <a   onClick={()=>this.downloadQueryForm(record)}>下载&nbsp;&nbsp;</a>
                <a   onClick={()=>this.deleteQueryForm(record)}>删除&nbsp;&nbsp;</a>
               </span>

                ),
            }
        ];
        return (
            <div className={styles.container}>
                <div className={styles.topContainer}>
                <div className={styles.top}>
                    {/*<Sign name="我的询单"/>*/}
                    <Breadcrumb>
                        <Breadcrumb.Item><Icon type="shopping" theme="filled" style={{color:"#6597FF",fontSize:"24px"}}/>我的询单</Breadcrumb.Item>
                    </Breadcrumb>
                    <div className={styles.right}>
                        <Search
                               placeholder="询单名/备注"
                               className={styles.search}
                               size="large"
                               onSearch={this.searchQuery}
                        />
                    </div>
                </div>
                </div>
                <Table
                       columns={columns} dataSource={this.state.data}
                       onChange={this.handleChange} pagination={false}
                       className={styles.table} size="middle"
                       loading={this.state.tableLoading}
                       rowKey="id"
                />
                <Pagination showSizeChanger
                            current={this.state.PaginationCurrent}
                            pageSize={this.state.PaginationPageSize}
                            onShowSizeChange={this.onShowSizeChange}
                            onChange={this.onChange}
                            showTotal={total => `系统里共 ${total} 个询单`}
                            defaultCurrent={1} total={this.state.total} />
                {/*查看询单弹出层*/}
                <QueryFormPopup
                    queryFormModalVisible={this.state.queryFormModalVisible}
                    queryFormRecord={this.state.queryFormRecord}
                    modalChange={newState=>{this.queryFormModalChange(newState)}}
                    orderInfo={this.state.orderInfo}
                    hardwareTopologyUrl={this.state.hardwareTopologyUrl}
                />
                {/*删除询单弹出层*/}
                <Modal
                    title="提示"
                    visible={this.state.deleteVisible}
                    onOk={this.deleteQueryFormOk}
                    okText="确认"
                    cancelText="取消"
                    onCancel={this.deleteQueryFormCancel}
                    width={420}
                    bodyStyle={{height:"193px"}}
                >
                    <p style={{paddingTop:"37px",textAlign:"left",color:"#101010",width:"305px",margin:"0 auto",fontSize:"16px"}}>您是否要删除询单:{this.state.deleteQueryFormRecord.name}？</p>
                </Modal>
            </div>
        );
    }

}

export default  QueryForm;